.file-explorer .drop-zone-overlay {
  backdrop-filter: blur(2px);
  animation: fadeIn 0.3s ease-out;
}
.file-explorer .loading-state {
  animation: pulse 2s infinite;
}
.file-explorer .empty-state {
  animation: slideUp 0.5s ease-out;
}
.file-explorer .empty-state .empty-icon {
  animation: bounce 2s infinite;
}
.file-explorer .files-grid {
  animation: fadeIn 0.3s ease-out;
}
.file-explorer .files-grid.grid-view {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
}
@media (min-width: 640px) {
  .file-explorer .files-grid.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  }
}
@media (min-width: 768px) {
  .file-explorer .files-grid.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }
}
@media (min-width: 1024px) {
  .file-explorer .files-grid.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  }
}
@media (min-width: 1280px) {
  .file-explorer .files-grid.grid-view {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}
.file-explorer .files-grid.list-view {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
